import { Controller } from '@ali/4ever-cangjie';
import { sample } from '@ali/4ever-bamboo';
import { eventTracking, TrackingType } from '../actions';

const trackPaste = sample((controller) => {
  Promise.resolve().then(() => {
    controller.run(
      'onAction',
      eventTracking({ type: TrackingType.paste }),
    );
  });
}, { sample: 10 })

export default function onPaste(event: React.ClipboardEvent, controller: Controller, next) {
  trackPaste(controller);

  return next();
}
